@import "../../variables.module.scss";

.container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 6;
  background-color: $color-secondary;

  .backIcon {
    color: $color-primary;
    margin: 13px 0 13px 20px;
    transform: rotate(90deg);
    cursor: pointer;
  }

  .projectInfo {
    margin: 0;
    padding: 20px 2vw 40px;
    background-color: $color-secondary;
    color: $color-primary;
    font-family: $font-secondary;
    box-shadow: 0px -12px 14px 0px rgba(0, 0, 0, 0.1);
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    transform: translateY(100%);
    transition: transform 0.8s;

    .closeButton {
      cursor: pointer;
      float: right;
    }

    h3 {
      font-family: $font-primary;
      text-align: center;
    }
    
    p {
      max-width: 600px;
      margin: 24px auto;
      line-height: 1.5;
    }

    .buttonsContainer {
      width: 100%;
      max-width: 600px;
      margin: 30px auto;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
  }

  .showProjectInfo {
    transform: translateY(0);
  }
}

@media screen and (min-width: $tablet) {
  .container{
    .projectInfo{
      padding: 20px 11vw 40px;
    }
  }
}