@import "../../variables.module.scss";

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  max-width: 600px;
  margin: 20px auto;
}

.stackIcon {
  width: 30px;
  height: 30px;

  margin: 0 4px;
}

@media screen and (min-width: $tablet) {
  .container {
    justify-content: flex-start;
    h5 {
      margin-right: 30px;
    }
  }
}
